.Wrapper{
	border: 1px solid #777676;
	width: 400px;
	min-height: 45vh;
	box-shadow: 0 2px 4px rgba(0, 0, 0, .2);
	border-radius: 15px;
	padding-bottom: 20px;
	/*flex-wrap: wrap;*/
	margin: 20px 20px;
}
.Wrapper h2{
	display: block;
	text-align: center;
	/*margin: 10px 40%;*/
	/*color: mediumpurple;*/
	letter-spacing: 1px;
}
.Wrapper h3{
	letter-spacing: 1px;
	margin-bottom: 0px;
	padding-left: 10px;
}
.News{
	display: flex;
	flex-direction: row;
	justify-content: space-around;
	flex-wrap: wrap;
	align-items: center;
}
.New{
	width: 380px;
	letter-spacing: .03em;
}
.Buttons{
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	margin-top: 20px;
}
.Btns{
	width: 80%;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
}
@media (min-width: 355px) and (max-width:769px) {
	.Wrapper{
		margin: 20px auto;
		border: 1px solid purple;
		max-width: 90%;
		/*margin: 20px 10px;*/
	}
	.Buttons{
		width: 100%;
		margin-top: 20px;
	}
	.Btns{
		width: 90%;
		display: flex;
		flex-direction: row;
		justify-content: space-around;
	}
}
@media (min-width: 279px) and (max-width:354px) {
	.Wrapper{
		margin: 20px auto;
		border: 1px solid purple;
		max-width: 90%;
		/*margin: 20px 10px;*/
	}
	.Buttons{
		width: 100%;
		margin-top: 20px;
	}
	.Btns{
		width: 90%;
		display: flex;
		flex-direction: row;
		justify-content: space-around;
	}
}